    async updateCategoryOptions() {
      let result = await getProfileByOwnerAddress(this.currentAccount);
      console.log(result);
      let categoryOptionsArr = [];
      var reg = /\w+\.\d+/g;
      for (let i = 0; i < result.profiles.length; i++) {
        console.log(result.profiles[i].handle.match(reg));
        if (result.profiles[i].handle.match(reg) !== null) {
          let tmp = {
            label: result.profiles[i].handle.replace(/\.\d+/g, ""),
            value: result.profiles[i].profileId,
          };
          categoryOptionsArr.push(tmp);
        }
