      let result = await this.getPostsAllByProfileID(id);
      console.log(result.posts);
      let posts = result.posts;
      this.ProfilePosts[id] = [];
      this.currentProfilePosts = [];
      let textREG = /text:/g;
      let imgREG = /img:/g;
      let tiREG = /ti:/g;
      let linkREG = /link:/g;
      for (let i = 0; i < posts.length; i++) {
        if (posts[i].contentURI.match(tiREG) !== null) {
          posts[i].type = "ti";
          let tmp = posts[i].contentURI
            .replace(tiREG, "")
            .replace(imgREG, "")
            .split(",text:");
          posts[i].data = {
            text: tmp[1],
            img: tmp[0],
          };
        } else if (posts[i].contentURI.match(linkREG) !== null) {
          posts[i].type = "link";
          let tmp = posts[i].contentURI.replace(linkREG, "").split(",text:");
          posts[i].data = {
            link: tmp[0],
            text: tmp[1],
          };
        } else if (posts[i].contentURI.match(textREG) !== null) {
          posts[i].type = "text";
          posts[i].data = {
            text: posts[i].contentURI.replace(textREG, ""),
          };
        } else if (posts[i].contentURI.match(imgREG) !== null) {
          posts[i].type = "img";
          posts[i].data = {
            img: posts[i].contentURI.replace(imgREG, ""),
          };
        } else {
          posts[i].type = "null";
        }
        this.ProfilePosts[id].push(posts[i]);
        this.currentProfilePosts.push(posts[i]);
      }
      console.log(this.currentProfilePosts);
    },
    // web3
    sendMsgTest() {
      this.sendMsg(this, "Success", "suddcess msg", "success");
      this.sendMsg(this, "Warning", "warning msg", "warning");
    },
    async getData() {
      const results = await getAllPosts();
      console.log(results);
    },
    async getProfileByOwnerAddress() {
      let result = await getProfileByOwnerAddress(this.currentAccount);
      console.log(result);
      let userProfiles = [];
      var reg = /\w+\.\d+/g;
      var colorReg = /color:/g;
      var urlReg = /url:/g;
      for (let i = 0; i < result.profiles.length; i++) {
        console.log(result.profiles[i].handle.match(reg));
        if (result.profiles[i].handle.match(reg) !== null) {
          console.log(result.profiles[i].imageURI.match(colorReg));
          if (result.profiles[i].imageURI.match(colorReg) !== null) {
            result.profiles[i].type = "color";
            result.profiles[i].data = result.profiles[i].imageURI.replace(
              colorReg,
              ""
            );
          } else if (result.profiles[i].imageURI.match(urlReg) !== null) {
            result.profiles[i].type = "url";
            result.profiles[i].data = result.profiles[i].imageURI.replace(
              urlReg,
              ""
            );
          } else {
            result.profiles[i].type = "null";
          }
          userProfiles.push(result.profiles[i]);
        }
      }
      console.log(userProfiles);
      this.userProfiles = userProfiles;
    },
    async getProfileByPage() {
      let first = 10;
      let skip = 0;
      let result = await getProfileByPage(first, skip);
      console.log(result);
    },
    async getPostsByProfileID(id) {
      let result = await getPostsByProfileID(id);
      console.log(result);
    },
    async getProfileIDFromProfileHandle() {
      let result = await getProfileIDFromProfileHandle();
      console.log(result);
    },
    async getPostsAllByProfileID(profileId) {
      let result = await GetPostsAllByProfileID(profileId);
      console.log(result);
      return result;
    },
    setTxPendingFlag(bool) {
      this.TxPendingFlag = bool;
    },
    setIsConnected(bool) {
      this.isConnected = bool;
    },
    async post() {
      let my = this;
      if (this.currentAccount === "") {
        // alert("Please re-connect your wallet.");
        my.sendErrorMsg(my, "Please re-connect your wallet!");
      } else {
        if (
          this.selectedCategoryOptions == null ||
          this.selectedCategoryOptions == ""
        ) {
          my.sendErrorMsg(my, "Please Select Category Name!");
          return;
        }
        console.log(this.selectedCategoryOptions);
        console.log(this.selectedPostOptions);
        console.log(this.postTextParam);
        console.log(this.postOtherParam);
        let contentTmp = "";
        if (
          (this.selectedPostOptions != null) &
          (this.selectedPostOptions >= 0)
        ) {
          switch (this.selectedPostOptions) {
            case 0:
              if (this.postOtherParam == null || this.postOtherParam == "") {
                my.sendErrorMsg(my, "Please input Image URL!");
                return;
              }
              contentTmp = "img:" + this.postOtherParam;
              break;
            case 1:
              if (this.postTextParam == null || this.postTextParam == "") {
                my.sendErrorMsg(my, "Please input Text!");
                return;
              }
              contentTmp = "text:" + this.postTextParam;
              break;
            case 2:
              if (this.postTextParam == null || this.postTextParam == "") {
                my.sendErrorMsg(my, "Please input Text!");
                return;
              }
              if (this.postOtherParam == null || this.postOtherParam == "") {
                my.sendErrorMsg(my, "Please input Image URL!");
                return;
              }
              contentTmp =
                "ti:img:" + this.postOtherParam + ",text:" + this.postTextParam;
              break;
            case 3:
              if (this.postTextParam == null || this.postTextParam == "") {
                my.sendErrorMsg(my, "Please input Text!");
                return;
              }
              if (this.postOtherParam == null || this.postOtherParam == "") {
                my.sendErrorMsg(my, "Please input website URL!");
                return;
              }
              contentTmp =
                "link:" + this.postOtherParam + ",text:" + this.postTextParam;
              break;
            default:
              break;
          }
        } else {
          my.sendErrorMsg(my, "Please Select Post Type!");
          return;
        }
        const inputStruct = {
          profileId: this.selectedCategoryOptions,
          contentURI: contentTmp,
          collectModule: contractAddr.emptyCollectModule,
          collectModuleData: [],
          referenceModule: contractAddr.ZERO_ADDRESS,
          referenceModuleData: [],
        };
        try {
          const lensHubProxyContract = new this.web3.eth.Contract(
            abis.lensHubProxy,
            contractAddr.lensHubProxy
          );
          if (lensHubProxyContract) {
            this.setTxPendingFlag(true);
            // call to contract post
            lensHubProxyContract.methods
              .post(inputStruct)
              .send({
                from: this.currentAccount,
              })
              .on("receipt", function (receipt) {
                console.log(receipt);
                my.setTxPendingFlag(false);
                if (receipt.status) {
                  console.log("success");
                  my.sendMsg(my, "Success", "Created successfully", "success");
                } else {
                  my.sendErrorMsg(my, "Transaction failed!");
                }
              })
              .on("error", function (error) {
                console.log(error);
                my.setTxPendingFlag(false);
                // alert("Transaction failed");
                my.sendErrorMsg(my, "Transaction failed!");
              });
          } else {
            // alert("contract error!");
            my.sendErrorMsg(my, "contract error!");
          }
        } catch (error) {
          // console.log(error)
          // alert("Error:Transaction failed!");
          my.sendErrorMsg(my, "Error:Transaction failed!");
        }
      }
    },
    async createProfile() {
      let my = this;
      if (this.currentAccount === "") {
        // alert("Please re-connect your wallet.");
        this.sendErrorMsg(this, "Please re-connect your wallet!");
      } else {
        if (
          this.createProfileParamHandle == null ||
          this.createProfileParamHandle == ""
        ) {
          my.sendErrorMsg(my, "Please input Category Name!");
          return;
        }
        if (
          this.createProfileParamImageURI == null ||
          this.createProfileParamImageURI == ""
        ) {
          my.sendErrorMsg(my, "Please select an option and input Image URL!");
          return;
        }

        var url = "";
        if (this.selectedOptions == 0) {
          url = "url:" + this.createProfileParamImageURI;
        } else if (this.selectedOptions == 1) {
          url = "color:" + this.createProfileParamImageURI;
        } else {
          url = this.createProfileParamImageURI;
        }
        let handleTmp =
          this.createProfileParamHandle.toLowerCase() +
          "." +
          new Date().getTime();
        const inputStruct = {
          to: this.currentAccount,
          handle: handleTmp,
          imageURI: url,
          followModule: contractAddr.ZERO_ADDRESS,
          followModuleData: [],
          followNFTURI: url,
        };
        console.log(inputStruct);
        try {
          // call to contract post
          const mockProfileCreationProxyContract = new this.web3.eth.Contract(
            abis.MockProfileCreationProxy,
            contractAddr.MockProfileCreationProxy
          );
          // const tx = await mockProfileCreationProxy.connect(signer).proxyCreateProfile(inputStruct)
          // let count = 0;
          if (mockProfileCreationProxyContract) {
            this.setTxPendingFlag(true);
            mockProfileCreationProxyContract.methods
              .proxyCreateProfile(inputStruct)
              .send({
                from: this.currentAccount,
              })
              .on("receipt", function (receipt) {
                console.log(receipt);
                my.setTxPendingFlag(false);
                // if(count==1){
                //   return
                // }
                if (receipt.status) {
                  console.log("success");
                  my.sendMsg(my, "Success", "Created successfully", "success");
                } else {
                  my.sendErrorMsg(my, "Transaction failed!");
                }
                // if (receipt.status) {
                //   count = 1
                //   console.log("success");
                //   const newProfileID = BigNumber.from(
                //     receipt?.logs[0]?.topics?.[3]
                //   )?.toString();
                //   console.log(
                //     my.createProfileParamHandle.toLowerCase() +
                //       " - ID #" +
                //       newProfileID
                //   );
                //   my.sendMsg(my, "Success", "Created successfully", "success");
                // } else {
                //   my.sendErrorMsg(my, "Transaction failed!");
                // }
              })
              .on("error", function (error) {
                console.log(error);
                my.setTxPendingFlag(false);
                // alert("Transaction failed");
                my.sendErrorMsg(my, "Transaction failed!");
              });
          } else {
            // alert("contract error!");
            my.sendErrorMsg(my, "contract error!");
          }
        } catch (error) {
          // console.log(error)
          // alert("Error:Transaction failed!Please use another Profile name!");
          my.sendErrorMsg(
            my,
            "Error:Transaction failed!Please use another Profile name!"
          );
        }
      }
    },
    async InitWeb3() {
      let my = this;
      let checkMetaMaskFlag = await this.checkMetaMask();
      console.log(checkMetaMaskFlag);
      if (checkMetaMaskFlag == 2) {
        await this.switch();
        if (this.switchChainFlag == 1) {
          let setWeb3ProviderFlag = await this.setWeb3Provider();
          if (setWeb3ProviderFlag == 1) {
            console.log(this.web3Provider);
            this.web3 = await new Web3(this.web3Provider);

            let accounts = await this.web3.eth.getAccounts();
            console.log(accounts);
            if (accounts.length == 0) {
              console.log("accounts is null");
              return;
            }
            this.currentAccount = accounts[0];
            await this.web3.eth.getBalance(accounts[0], (err, wei) => {
              if (!err) {
                let balance = my.web3.utils.fromWei(wei, "ether");
                console.log("Matic==", balance);
                console.log("web3Provider:", my.web3Provider);
                console.log("switchChainFlag:", my.switchChainFlag);
                console.log("web3:", my.web3);
              }
            });
            this.setIsConnected(true);
            my.getProfileByOwnerAddress();
            // /////////////////////////
            // todo:load nft list
            // /////////////////////////

            // listening to change accounts
            this.WinEthereum.on("accountsChanged", function (accounts) {
              if (accounts.length == 0) {
                return;
              }
              console.log(accounts[0]);
              my.currentAccount = accounts[0];
              my.web3.eth.getBalance(accounts[0], (err, wei) => {
                if (!err) {
                  let balance = my.web3.utils.fromWei(wei, "ether");
                  console.log("Matic==", balance);
                }
              });
              my.setIsConnected(true);
              my.getProfileByOwnerAddress();
              // /////////////////////////
              // todo:reload nft list
              // /////////////////////////
            });
          } else {
            console.log("setWeb3Provider error");
          }
        } else {
          console.log("switch chain error");
        }
      } else {
        console.log("checkMetaMask error");
      }
    },
    async setWeb3Provider() {
      if (this.WinEthereum) {
        this.web3Provider = this.WinEthereum;
        try {
          // request user to approve
          await this.WinEthereum.enable();
        } catch (error) {
          // 用户不授权时
          console.error("User denied account access");
          this.sendErrorMsg(this, "User denied account access!");
          return 0;
        }
      } else if (window.web3) {
        this.web3Provider = window.web3.currentProvider;
      } else {
        this.web3Provider = new Web3.providers.HttpProvider(polygonMumbaiRPC);
      }
      return 1;
    },
    async switch() {
      console.log(this.WinEthereum.chainId);
      if (this.WinEthereum.chainId == 0x13881) {
        this.switchChainFlag = 1;
        console.log("Polygon Mumbai");
        return;
      }
      await this.switchChain(polygonMumbai);
    },
    async addChain(data) {
      try {
        await this.WinEthereum.request({
          method: "wallet_addEthereumChain",
          params: [data],
        });
      } catch (addError) {
        console.log(addError);
        this.sendErrorMsg(this, "addChain failed!");
        // handle "add" error
      }
    },
    async switchChain(data) {
      try {
        let { chainId } = data;
        await this.WinEthereum.request({
          method: "wallet_switchEthereumChain",
          params: [{ chainId }],
        });
        this.switchChainFlag = 1;
      } catch (switchError) {
        // This error code indicates that the chain has not been added to MetaMask.
        console.log(switchError);
        this.sendErrorMsg(this, "switchChain failed!");
        if (switchError.code === 4902) {
          await this.addChain(data);
          this.switchChainFlag = 1;
        }
        // handle other "switch" errors
      }
    },
    async checkMetaMask() {
      if (this.WinEthereum) {
        if (typeof this.WinEthereum !== "undefined") {
          console.log("MetaMask is installed!");
          return 2;
        } else {
          console.log("can't find MetaMask！");
          this.sendErrorMsg(this, "can't find MetaMask！");
          return 1;
        }
      } else {
        console.log("nothing！");
        return 0;
      }
    },
    sendMsg(my, title, msg, type) {
      const h = my.$createElement;
      my.$notify({
        title: title,
        type: type,
        message: h("i", { style: "color: teal" }, msg),
      });
    },
    sendErrorMsg(my, msg) {
      let h = my.$createElement;
      my.$notify.error({
        title: "Error",
        message: h("i", { style: "color: red" }, msg),
      });
    },
  },
  computed: {
    noText() {
      if (this.search === "") return true;
      return false;
    },
    haveText() {
      if (this.search === "") return false;
      return true;
    },
  },
};
